
$(".navigation-trigger ").on('click',function(){
    $(".navigation-trigger").addClass('open');
    if($(".navigation-trigger").hasClass('open')){
        $(".navigation_ham_list").show();
        $('.header').animate({height:260},150 ,function(){
            $(".navigation_ham_list").addClass('open');
        });
        $(".navigation_ham_list").click(function(){
            $('.navigation_ham_list').hide();
            $('.header').animate({height:80},200);
            $(".navigation_ham_list").removeClass('open');
        })
    }

})
$('body').click(function(){
    var target = $( event.target);
    console.log(target);
    if(($(".navigation-trigger").hasClass('open'))){
        if(($(".navigation_ham_list").hasClass('open')) && !target.is($('.navigation__link'))){
            $('.navigation_ham_list').hide();
            $('.header').animate({height:80},200);
            $(".navigation-trigger").removeClass('open');
            $(".navigation_ham_list").removeClass('open');
        }  
    }
})


